<template>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      network: {
        'm': {
          'chainId': '0x89',
          'rpc': 'https://opt-mainnet.g.alchemy.com/v2/HRefLfsX10aUeeUq8tFJ6j87gsAjLujK',
          'chainName': 'Matic'
        },
        'b': {
          'chainId': '0x38',
          'rpc': 'https://bsc-dataseed1.ninicoin.io',
          'chainName': 'Binance Smart Chain Mainnet'
        },
        'arb': {
          'chainId': '0xA4B1',
          'rpc': 'https://arb-mainnet.g.alchemy.com/v2/sVvvIHCFGwEpoWF2aZP9mDlBxL9e4Vlu',
          'chainName': 'Arbitrum'
        },
        'op': {
          'chainId': '0xA',
          'rpc': 'https://opt-mainnet.g.alchemy.com/v2/HRefLfsX10aUeeUq8tFJ6j87gsAjLujK',
          'chainName': 'Optimism'
        },
      }
    }
  },
  mounted() {
    this.connect()
  },
  methods: {
    getUrlParam(paraName) {
      var url = document.location.toString();
      var arrObj = url.split("?");

      if (arrObj.length > 1) {
        var arrPara = arrObj[1].split("&");
        var arr;

        for (var i = 0; i < arrPara.length; i++) {
          arr = arrPara[i].split("=");

          if (arr != null && arr[0] == paraName) {
            return arr[1];
          }
        }
        return "";
      } else {
        return "";
      }
    },
    async connect() {
      const config = this.getUrlParam('c')
      try {
        await ethereum.request({
          method: 'eth_requestAccounts',
        });
        const chainId =  this.network[config].chainId
        try {
          await ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{chainId:chainId}],
          });
        } catch (switchError) {
          // This error code indicates that the chain has not been added to MetaMask.
          if (switchError.code === 4902) {
            try {
              await ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainId: chainId,
                    chainName: this.network[config].chainName,
                    rpcUrls: [this.network[config].rpc],
                  },
                ],
              });
            } catch (addError) {
              // handle "add" error
            }
          }
          // handle other "switch" errors
        }
      } catch (error) {
        console.error(error);
      }
    }
  },
}
</script>

